<template>
    <div class="wxz-container">
        <header-bar></header-bar>
        <div class="container-fluid">
            <div class="container">
                <div class="wxz-content clearfix"  v-for="(items,index) in worksDetail" v-bind:key="index" v-if="items.typId == worksId">
                    <div class="row">
                        <div class="col-xs-12 col-md-12 text-center">
                            <h3>{{items.title}}</h3>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-xs-12 col-md-12 text-center">
                            <img src="../../../static/images/line.png" class="img-responsive img-auto">
                        </div>
                    </div>
                    <div class="row" v-if="items.link.length!=0">
                        <div class="col-xs-12 col-md-12">
                            <ul class="list-inline">
                                <li>网址</li>
                                <li class="cur" @click="getOpenlink(items.link)">
                                    {{items.link}}
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-xs-12 col-md-12 pb20">
                            <img :src="items.pic" class="img-responsive">
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <footer-bar></footer-bar>
    </div>
</template>
<script>
import {headerBar,footerBar} from '../../components/export.js';
import "../../amtdata/workslistData.js";
export default{
    data(){
        return{
            worksDetail:{},
            worksId:this.$route.params.id,
        }
    },
    components:{
        headerBar,footerBar
    },
    mounted(){
        this.getworksDetailData();
    },
    methods:{
        getworksDetailData:function(){
            let vm = this
            vm.$axios.get('/api/workslsit').then(res=>{
                vm.worksDetail = res.data.worksData
            });
        },
        getOpenlink:function(item){
            window.open(item)
        }
    },
    
}
</script>


